@font-face {
    font-family: 'Folkard';
    src: url('./fonts/folkard_.ttf') format('truetype'); /* Ajusta la ruta según la ubicación */
    font-weight: normal;
    font-style: normal;
  }

/* Fondo general y diseño de la página */
.guild-container {
    min-height: 100vh;
    background-color: #121212;
    color: white;
    padding: 20px;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
}
  
  /* Aplicar la fuente en los estilos */
  .guild-title {
    font-family: 'Folkard', sans-serif; /* Usa la fuente Folkard */
    font-size: 48px;
    font-weight: bold;
    color: #ffd700; /* Amarillo dorado */
    margin-bottom: 5px;
  }
  
  .guild-subtitle {
    font-family: 'Folkard', sans-serif; /* Usa la fuente Folkard */
    font-size: 32px;
    font-weight: bold;
    color: #cccccc; /* Gris claro */
    margin-top: 0;
    margin-bottom: 5px;
  }
  
  .guild-warning {
    font-family: 'Folkard', sans-serif; /* Usa la fuente Folkard */
    font-size: 24px;
    color: red;
    font-weight: bold;
    margin-top: 0;
    margin-bottom: 20px;
  }
  

/* Contenedores de las barras laterales y la tabla */
.sidebars {
    font-family: 'Merriweather', serif;
    display: flex;
    justify-content: space-between;
    width: 100%;
    max-width: 1200px; /* Limitar el ancho máximo */
    margin-top: 20px;
    gap: 20px; /* Añadir espacio entre los contenedores */
}

/* Barra izquierda con roles y clases */
.left-sidebar {
    width: 22%; /* Reducir el ancho de la barra izquierda */
    text-align: left;
    padding: 10px;
    background-color: #222;
    border-radius: 8px;
}

/* Barra derecha con profesiones */
.right-sidebar {
    width: 22%; /* Reducir el ancho de la barra derecha */
    text-align: left;
    padding: 10px;
    background-color: #222;
    border-radius: 8px;
}

/* Contenedor de la tabla */
.table-container {
    width: 50%; /* Ajustar la tabla para que ocupe el 50% del espacio */
    margin: 0; /* Eliminar márgenes innecesarios */
    text-align: center;
    background-color: #222;
    border-radius: 8px;
    padding: 10px;
}

/* Estilos de la tabla */
.guild-table {
    width: 100%;
    border-collapse: collapse;
    border: 2px solid #555;
    background-color: #222;
}

.guild-table th,
.guild-table td {
    border: 1px solid #444;
    padding: 10px;
    text-align: center;
}

.guild-table th {
    background-color: #333;
    color: #ddd;
    font-weight: bold;
}

.guild-table tr:hover {
    background-color: #444;
}

/* Estilos de los roles */
.role-cell {
    font-size: 18px;
}

.role-image {
    width: 40px;
    height: 40px;
    border-radius: 50%; /* Hace que la imagen sea circular */
}

/* Para mostrar el nombre de Twitch sin enlace */
.twitch-text {
    color: #9146ff;
    font-weight: bold;
}

/* Listas sin viñetas */
.role-list,
.class-list,
.profession-list {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.role-list div,
.class-list div,
.profession-list div {
    color: #ddd;
    margin-bottom: 10px;
}
